@import './variables.scss';

.heading {
    display: flex;
    flex-direction: column;
    width: 100%;
}

h1 {
    color: white;
    font-size: 4em;
    margin-bottom: 20px;
}

span {
    color: $accent_color;
    margin-left: 1%;
}

nav {
    display: flex;
    flex-direction: row;
    margin-bottom: 14px;
}

.menu__btn {
    background-color: $dark_background;
    border: none;
    color: white;
    font-size: 1.5em;
    font-weight: 800;
    height: 40px;
    margin-right: 16px;
    outline: none;
    width: 41%;
      &:hover {
        background-color: $hover_color;
        border-radius: 18px 0 0 18px;
        cursor: pointer;
        font-weight: 900;
        transform: scale(1.1);
        transition: all .1s ease-in-out;
      }
}

svg {
    margin-left: 3%;
}